@font-face {
    font-family: 'vaporWave';
    src: url('../assets/fonts/Over-Break.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'horror';
    src: url('../assets/fonts/StickyPops.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.container {
    background-size: cover;
    display: flex;
    position: relative;
    justify-content: center;
    min-height: 130vh;
    overflow-y: hidden;
    text-align: center;
    width: 100%;

    .noisy {
        position: absolute;
        background-image: url(data:image/png;base64,);
        min-width: 100%;
        min-height: 200vh;
        top: -50px;
        z-index: 4;
    }

    .title {
        font-size: 60px;
        text-shadow: #90447D 10px 0 5px, #31C8D2 20px 0 10px;
        font-family: vaporWave;
        color: white;
    }

    .name {
        font-size: 60px;
        color: white;
        font-family: horror;
        letter-spacing: 4px;
        text-shadow: -1px 0 #31C8D2, 0 2px #31C8D2, 2px 0 #31C8D2, 0 -1px #31C8D2;
    }

    .vhs {
        width: 150px;
        z-index: 4;

        &:hover{
            cursor: pointer;
            -webkit-filter: drop-shadow(2px 1px 10px #31C8D2)
                drop-shadow(-1px -1px 10px #31C8D2);
            filter: drop-shadow(2px 1px 10px #31C8D2)
                drop-shadow(-1px -1px 10px #31C8D2);
        }
    }

    .face {
        position: absolute;
        width: 40%;
        top: 250px;
        z-index: 2;
        cursor: pointer;
            -webkit-filter: drop-shadow(2px 1px 10px #37DCA0)
            drop-shadow(-1px -1px 10px #37DCA0);
        filter: drop-shadow(2px 1px 10px #37DCA0)
            drop-shadow(-1px -1px 10px #37DCA0);
    }

    .mountains {
        width: 120vw;
        position: absolute;
    }
}